.App {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contenedor{
  padding: 1rem 0 2rem 0;
  border-style: none;
  border-radius: .5rem;
  box-shadow: 0 0 .5rem white;
  width: 97%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  -webkit-border-radius: .5rem;
  -moz-border-radius: .5rem;
  -ms-border-radius: .5rem;
  -o-border-radius: .5rem;
}

.contenedor__titulo{
  color: white;
  margin: 0 0 3rem 0 ;
  text-shadow: 0 0 .3rem white;
  font-size: 2.5rem;
}
.contenedor__imgagen1{
  border-radius: .5rem;
  width: 85%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  background-color: bisque;
  -webkit-border-radius: .5rem;
  -moz-border-radius: .5rem;
  -ms-border-radius: .5rem;
  -o-border-radius: .5rem;
  margin-bottom: 1rem;
}

.contenedor__imagen1--imagen{
  width: 40rem;
  border-radius: .5rem;
  -webkit-border-radius: .5rem;
  -moz-border-radius: .5rem;
  -ms-border-radius: .5rem;
  -o-border-radius: .5rem;
  box-shadow: 0 0 .7rem white;
}

.contenedor__section1{
  border-radius: .5rem;
  background-color: darkorange;
  width: 89.5%;
  height: 5rem;
  -webkit-border-radius: .5rem;
  -moz-border-radius: .5rem;
  -ms-border-radius: .5rem;
  -o-border-radius: .5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: bold;
  color:#342d50;
}

.saludo{
  width: 100%;
  background-color: blue;
  height: 5rem;
  color: black;
}




/* responsive */
@media only screen and (max-width:768px) {
  .contenedor{
    box-shadow: none;
    justify-content: space-between;
  }
  .contenedor__imgagen1{
    width: 100%;
  }
  .contenedor__imagen1--imagen{
    width: 20rem;
   
  }
 
  .contenedor__titulo{
    
    font-size: 2.1rem;
    text-align: center;
  }
  .contenedor__section1{
    width: 100vh;
    
    display: flex;
    align-items: center;
    justify-content: center;
    
    
  }
  .contenedor__section1__parrafo{
    font-size: 1rem;
    font-weight: bold;
    
  }
}


